import Vue from 'vue'
import App from './App'
import router from './router'
import './scss/main.scss'

import taskHeader from './components/task-header.vue'

Vue.component(taskHeader.name, taskHeader);

Vue.config.productionTip = false;

new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



