import Vue from 'vue'
import Router from 'vue-router'
import MyTaskIndex from '../view/mytask/index.vue'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      redirect : '/mytask'
    },
    {
      path: '/mytask',
      name: 'mytask',
      component: MyTaskIndex
    }
  ]
})



